













import { Component, Watch, Prop } from 'vue-property-decorator';
import EventBus from '@/EventBus';

import FSBase from '@/components/_old/FSBase.vue';
import { Sketch } from 'vue-color';

@Component({
  components: {
    Sketch,
  }
})
export default class FSMenu extends FSBase {
  @Prop({default: false}) private menuOpen !: boolean;

  @Watch('menuOpen')
  onMenuOpen(v: boolean){
    if(!v){
      this.passThrough = false;
    }
  }

  private passThrough = false;
  private colors = '#F00';

  $refs !: {
    menu: HTMLElement;
  };

  private colorUpdate(e: object){
    EventBus.$emit('colorUpdate', e);
  }

  mounted(){
    const menu = this.$refs.menu;

    menu.addEventListener('contextmenu', (e) => {
      this.passThrough = !this.passThrough;
      e.preventDefault();
    })
    //
  }
}
