





import EventBus from '@/EventBus';
import FSPoint from '@/intern/_old/FSPoint';

// eslint-disable-next-line
// const OffWorker = require('worker-loader!@/workers/offscreen.worker.js');

import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

@Component({})
export default class FS2DCanvas extends Vue{
  @Prop({default: () => { return null }}) color!: any;

  $refs!: {
    can: HTMLCanvasElement[];
  }

  private worker!: Worker;
  private point: FSPoint | null = new FSPoint();
  private pnts!: FSPoint[];
  private offscreen!: any;
  private pressure!: number;
  private eraser = false;

  private lineBuffer = 5;

  @Watch('point', {deep: true})
  private pointChange(n, o){
    this.pnts.push(n);
    if(n && o && this.pnts.length >= this.lineBuffer + 1){
      this.draw(this.pnts.slice(-this.lineBuffer - 1, -1), 1);
    }
  }

  destroyed(){
    EventBus.$off('pIn');
    EventBus.$off('pOut');
    EventBus.$off('pUp');
    EventBus.$off('pDn');
    EventBus.$off('pMv');
  }

  private draw(pnts, id){
    const ctx = this.$refs.can[1].getContext('2d') as CanvasRenderingContext2D;
    if(this.color.rgba){
      ctx.fillStyle = "rgba(" +
        this.color.rgba.r + "," +
        this.color.rgba.g + "," +
        this.color.rgba.b + "," +
        this.color.rgba.a + ")";
    }else{
      ctx.fillStyle = "#000";
    }

    // console.log(pnts);

    const p1 = new FSPoint(pnts[0].x, pnts[0].y);
    const p2 = new FSPoint(pnts[pnts.length-1].x, pnts[pnts.length-1].y);
    const amt = ((this.pressure * 8) / 7);
    const dist = p1.to(p2).dist * amt;

    // ctx.moveTo(p1.x, p1.y);
    ctx.moveTo(p1.x + dist, p1.y + dist);
    /*
    for(let i = 0; i <= pnts.length-1; i++){
      const p3 = new FSPoint(pnts[i].x, pnts[i].y);
      ctx.lineTo(p3.x + dist , p3.y + dist );
    }
    */
    ctx.lineTo(p2.x + dist, p2.y + dist);
    ctx.lineTo(p2.x - dist, p2.y - dist);
    /*
    for(let i = 0; i <= pnts.length-1; i++){
      const p3 = new FSPoint(pnts[i].x, pnts[i].y);
      ctx.lineTo(p3.x - dist  , p3.y - dist );
    }
    */
    ctx.lineTo(p1.x - dist, p1.y - dist);

    ctx.closePath();
    ctx.fill();

    /*

    if(this.eraser){
      ctx.globalCompositeOperation = 'destination-out';
    }else{
      ctx.globalCompositeOperation = 'source-over';
    }


    */
  }

  private clear(idx: number){
    const can = this.$refs.can[idx];
    const ctx = can.getContext('2d') as CanvasRenderingContext2D;
    ctx.clearRect(0, 0, can.width, can.height);
  }

  mounted(){
    this.$refs.can.forEach(can => {
      can.width = window.innerWidth;
      can.height = window.innerHeight;
    });

    document.addEventListener('keyup', (e) => {
      if(e.which == 82){
        this.eraser = !this.eraser;
      }
    })

    EventBus.$on('pIn', (e) => {
      // this.pnts.push(new FSPoint(e.offsetX, e.offsetY));
    });

    EventBus.$on('pOut', (e) => {
      this.pnts.push(new FSPoint(e.offsetX, e.offsetY));
      this.pnts = [];
      this.point = null;
    });

    EventBus.$on('pDn', (e) => {
      this.pnts = [];
      //
    });

    EventBus.$on('pUp', (e) => {
      this.$refs.can[0].getContext('2d').drawImage(this.$refs.can[1], 0, 0);
      this.clear(1);

      this.pnts = [];
      this.point = null;
    });

    EventBus.$on('pMv', (e) => {
      if(e.pressure){
        this.pressure = e.pressure;
        this.point = new FSPoint(e.offsetX, e.offsetY);
      }
    });

  }

