






import { Component, Vue } from 'vue-property-decorator';
import EventBus from '@/EventBus';

@Component
export default class FSDebug extends Vue{
  private pointerType = "";

  mounted(){
    EventBus.$on('pMv', (e: PointerEvent) => {
      this.pointerType = e.pointerType;
    })
  }

  destroyed(){
    EventBus.$off('pMv');
  }
}
