



































import { Component, Vue } from 'vue-property-decorator';
import EventBus from '@/EventBus';

// -- TODO REFACTOR --
import FSPoint from '@/intern/_old/FSPoint';
import FSObject from '@/intern/_old/FSObject';

import FSDebug from '@/components/FSDebug.vue';
import FSEvent from '@/components/FSEvent.vue';
import FSCursor from '@/components/FSCursor.vue';

import FSCanvasWebGL from '@/components/canvas/FSCanvasWebGL.vue';
import FS2DCanvas from '@/components/_old/FS2DCanvas.vue';

import FSMenu from '@/components/menu/FSMenu.vue';

// import FSBase from '@/components/FSBase.vue';
import FSBase from '@/components/_old/FSBase.vue';

@Component({
  components: {
    FSDebug,
    FSEvent,
    FSCursor,
    FSCanvasWebGL,
    FS2DCanvas,
    FSMenu,
    FSBase,
    // FSChild
  },
})
export default class App extends Vue {

  private debug = false;
  private menu = new FSObject();
  private menuOpen = false;

  private renderCan !: HTMLCanvasElement;

  private clearLayer(){
    const can = this.$refs.canvas as FSCanvasWebGL;
    can.clear();
  }

  /*== TODO: Seperate this into save module. */
  private setTransparentToColor(img, color = 255){
    for(let i = 0; i <= img.data.length; i+=4){
      if(img.data[i+3] < 255){
        img.data[i+0] = 255;
        img.data[i+1] = 255;
        img.data[i+2] = 255;
        img.data[i+3] = 255;
      }
    }

    this.renderCan!.getContext('2d').putImageData(img, 0, 0);
  }

  private setURL(){
    const url = this.renderCan.toDataURL('image/png');
    let fURL;
    fetch(url)
    .then(res => res.blob())
    .then( blob => {
      fURL = URL.createObjectURL(blob);
      window.open(fURL)
    });
  }

  private saveIMG(){
    const can = this.$refs.canvas as FSCanvasWebGL;
    const ctx = can.$refs.can[0];
    const w = this.renderCan.width;
    const h = this.renderCan.height;
    const img = ctx.getContext('2d').getImageData(0, 0, w, h);
    // this.setTransparentToColor(img);
    this.renderCan!.getContext('2d').putImageData(img, 0, 0);
    this.setURL();
  }
  /* == */

  mounted(){
    //TODO: GET RID
    this.renderCan = this.$refs.render as HTMLCanvasElement;
    this.renderCan.width = window.innerWidth;
    this.renderCan.height = window.innerHeight;
    //

    this.debug = EventBus.debug;
    this.menu = new FSObject({
      xy: new FSPoint(window.innerWidth/2, window.innerHeight- 25),
      wh: new FSPoint(80, 20)
    });

    // Todo a lot of this functionality should live in the menu.
    EventBus.$on('context', (e) => {
      if(this.menuOpen){
        this.menu.wh = new FSPoint(80, 20);
        this.menu.xy = new FSPoint(window.innerWidth/2, window.innerHeight- 25);
      }else{
        this.menu.wh = new FSPoint(200, 200);
        this.menu.xy = new FSPoint(e.offsetX + 50, e.offsetY - 100);
      }
      this.menuOpen = !this.menuOpen;
    });
  }


}
