






import { Component, Prop } from 'vue-property-decorator';
import FSBaseCanvas from './FSBaseCanvas.vue'
import EventBus from '@/EventBus';
import FSPoint from '@/intern/_old/FSPoint';

import BasicFrag from '!!raw-loader!@/intern/shaders/BasicBrush.fs';
import BasicVert from '!!raw-loader!@/intern/shaders/BasicBrush.vs';

import * as glHelper from '@/intern/glHelper';

@Component
export default class FSCanvasWebGL extends FSBaseCanvas {

  $refs!: {
    can: HTMLCanvasElement[];
  };

  public size = 8;
  public brushMax = 10;

  protected can!: HTMLCanvasElement;
  private points!: FSPoint[];
  private sPoints!: FSPoint[];
  private bufferArr = [];
  private pressure = 0;
  private color = {r: 255, g: 0, b: 0, a: 1};
  private lineBuffer = 9;
  private mult = 1;

  private point!: FSPoint;
  private p2!: FSPoint | null;

  private md = false;

  private gl!: WebGL2RenderingContext;
  private program!: WebGLProgram;
  private col!: any;
  private pos!: any;
  private pos2!: any;
  private matrix!: any;

  private pos2u!: any;
  private mDelta!: FSPoint;
  private dist!: any;
  private lD = 0;
  private lastDist!: any;

  mounted(){
    this.points = [];
    this.sPoints = [];

    const ctx = this.$refs.can[0].getContext('2d');

    ctx.fillStyle="#FFF";
    ctx.fillRect(0,0,window.innerWidth, window.innerHeight);

    const gl = this.$refs.can[1].getContext('webgl2', {alpha: 1}) as WebGL2RenderingContext;
    this.gl = gl;

    this.$refs.can.forEach((el) => {
      el.width = window.innerWidth;
      el.height = window.innerHeight;
    });

    this.point = new FSPoint(window.innerWidth/2, window.innerHeight/2);

    this.setupGL();
    this.setGeometry();

    EventBus.$on('colorUpdate', (e: any)=>{
      this.color.r = e.rgba.r / 255;
      this.color.g = e.rgba.g / 255;
      this.color.b = e.rgba.b / 255;
      this.color.a = e.rgba.a;
    });

    EventBus.$on('pOut', (e) => {
      this.points.push(new FSPoint(e.offsetX, e.offsetY));
      this.points = [];
      this.point = null;
    });

    EventBus.$on('pUp', (e: PointerEvent) => {

      this.draw();
      const can = this.$refs.can[0].getContext('2d', {alpha: 0}) as CanvasRenderingContext2D;
      can.drawImage(this.$refs.can[1], 0, 0);

      this.point = new FSPoint(e.offsetX, e.offsetY);
      this.points.push(new FSPoint(e.offsetX, e.offsetY));
      this.md = false;
      this.points = [];
      this.p2 = null;
    });

    EventBus.$on('pDn', (e: PointerEvent) => {
      this.points.push(new FSPoint(e.offsetX, e.offsetY));
      this.point = new FSPoint(e.offsetX, e.offsetY);
      this.md = true;

      requestAnimationFrame(this.update);
    });


    EventBus.$on('pMv', (e: PointerEvent) => {
      if(e.pressure){
        this.point = new FSPoint(e.offsetX, e.offsetY);
        this.mDelta = new FSPoint(e.movementX, e.movementY);
        this.points.push(this.point);
        this.pressure = e.pressure;
      }
    });
  }

  private update(){
    if(this.md){
      this.draw();
      requestAnimationFrame(this.update);
    }
  }

  private setupGL(){
    const gl = this.gl;

    const vShader = glHelper.createShader(gl, BasicVert, gl.VERTEX_SHADER);
    const fShader = glHelper.createShader(gl, BasicFrag, gl.FRAGMENT_SHADER);
    const program = glHelper.createProgram(gl, vShader, fShader);

    this.col = gl.getUniformLocation(program, 'v_color');
    const pos = gl.getAttribLocation(program, 'a_position');

    this.matrix = gl.getUniformLocation(program, 'u_matrix');
    this.pos2u = gl.getUniformLocation(program, 'u_lastPosition');
    this.dist = gl.getUniformLocation(program, 'u_distance');
    this.lastDist = gl.getUniformLocation(program, 'u_lastDistance');

    this.program = program;

    const buffer = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, buffer);
    gl.enableVertexAttribArray(pos);
    gl.vertexAttribPointer(pos, 2, gl.FLOAT, false, 0, 0);
  }

  private setGeometry(){
    const gl = this.gl;

    const vert = new Float32Array([
      0, 0,
      0, 0,
      0, 0,
      0, 0,
    ]);

    gl.bufferData(gl.ARRAY_BUFFER, vert, gl.STATIC_DRAW);
  }

  private glDraw(p, p2, size = this.size, color = this.color){

    if(!p2){
      p2 = p;
    }

    const gl = this.gl;
    const amt = ((this.pressure * size) / this.brushMax);
    // const amt = size;
    const dist = p.to(p2).dist * amt;

    let matrix = glHelper.m3.identity();

    gl.uniform4fv(this.col, [
      color.r,
      color.g,
      color.b,
      color.a
    ]);
    gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);
    gl.useProgram(this.program);

    gl.uniform1f(this.dist, dist);
    gl.uniform1f(this.lastDist, this.lD);
    gl.uniform2f(this.pos2u, p2.x - p.x, p2.y - p.y);

    matrix = glHelper.m3.projection(gl.canvas.width, gl.canvas.height);
    matrix = glHelper.m3.translate(matrix, p.x, p.y);

    gl.uniformMatrix3fv(this.matrix, false, matrix);
    gl.drawArrays(gl.TRIANGLE_STRIP, 0, 4);
    this.lD = dist;
  }

  public clear(){
    const can = this.$refs.can[0];
    can.getContext('2d').clearRect(0,0,window.innerWidth, window.innerHeight);
  }

  private draw(){
    this.gl.clear(this.gl.COLOR_BUFFER_BIT);
    const pts = this.points.slice(-9, -1);

    if(pts.length > 1){
      pts.forEach((el, idx) => {
        this.glDraw(pts[idx], pts[idx-3]);
      });
    }

    const can = this.$refs.can[0].getContext('2d') as CanvasRenderingContext2D;
    can.drawImage(this.$refs.can[1], 0, 0);
  }
}
