/*
  A helper utility that does many gl boilerplate stuff.

  - Shader compiler / Program Linker: https://webgl2fundamentals.org/webgl/lessons/webgl-boilerplate.html
  - M3 object: https://webgl2fundamentals.org/webgl/lessons/webgl-2d-matrices.html
*/
export const m3 = {
  /**
   * Multiplies two matricies together.
   *
   * @param {number[]} a first matrix.
   * @param {number[]} b second matrix.
   * @return {number[]} result matrix.
   */
  multiply: function(a: number[], b: number[]): number[] {
    const a00 = a[0 * 3 + 0];
    const a01 = a[0 * 3 + 1];
    const a02 = a[0 * 3 + 2];
    const a10 = a[1 * 3 + 0];
    const a11 = a[1 * 3 + 1];
    const a12 = a[1 * 3 + 2];
    const a20 = a[2 * 3 + 0];
    const a21 = a[2 * 3 + 1];
    const a22 = a[2 * 3 + 2];
    const b00 = b[0 * 3 + 0];
    const b01 = b[0 * 3 + 1];
    const b02 = b[0 * 3 + 2];
    const b10 = b[1 * 3 + 0];
    const b11 = b[1 * 3 + 1];
    const b12 = b[1 * 3 + 2];
    const b20 = b[2 * 3 + 0];
    const b21 = b[2 * 3 + 1];
    const b22 = b[2 * 3 + 2];

    return [
      b00 * a00 + b01 * a10 + b02 * a20,
      b00 * a01 + b01 * a11 + b02 * a21,
      b00 * a02 + b01 * a12 + b02 * a22,
      b10 * a00 + b11 * a10 + b12 * a20,
      b10 * a01 + b11 * a11 + b12 * a21,
      b10 * a02 + b11 * a12 + b12 * a22,
      b20 * a00 + b21 * a10 + b22 * a20,
      b20 * a01 + b21 * a11 + b22 * a21,
      b20 * a02 + b21 * a12 + b22 * a22,
    ];
  },

  /**
   * Creates translation matrix.
   *
   * @param {number} tx x translation.
   * @param {number} ty y translation.
   * @return {number[]} translation matrix.
   */
  translation: function(tx: number, ty: number): number[] {
    return [
      1, 0, 0,
      0, 1, 0,
      tx, ty, 1,
    ];
  },

  /**
   * Creates rotation matrix.
   *
   * @param {number} c cosine angle.
   * @param {number} s sine angle.
   * @return {number[]} rotation matrix.
   */
  rotation: function(c: number, s: number): number[] {
    return [
      c,-s, 0,
      s, c, 0,
      0, 0, 1,
    ];
  },

  /**
   * Creates scale matrix.
   *
   * @param {number} sx x scale.
   * @param {number} sy y scale.
   * @return {number[]} scale matrix.
   */
  scaling: function(sx: number, sy: number): number[] {
    return [
      sx, 0, 0,
      0, sy, 0,
      0, 0, 1,
    ];
  },

  /**
   * Creates matrix identity.
   *
   * @return {number[]} matrix.
   */
  identity: function (): number[] {
    return [
      1, 0, 0,
      0, 1, 0,
      0, 0, 1,
    ];
  },

  /**
   * Creates projection matrix.
   *
   * @param {number} width screen width.
   * @param {number} height screen height.
   * @return {number[]} projection matrix.
   */
  projection: function (width: number, height: number): number[] {
    // Note: This matrix flips the Y axis so that 0 is at the top.
    return [
      2 / width, 0, 0,
      0, -2 / height, 0,
      -1, 1, 1,
    ];
  },

  translate: function(m: number[], tx: number, ty: number): number[] {
    return m3.multiply(m, m3.translation(tx, ty));
  },

  rotate: function(m: number[], cos: number, sin: number): number[] {
    return m3.multiply(m, m3.rotation(cos, sin));
  },

  scale: function(m: number[], sx: number, sy: number): number[] {
    return m3.multiply(m, m3.scaling(sx, sy));
  },
}

/**
 * Creates and compiles a shader.
 *
 * @param {!WebGLRenderingContext} gl The WebGL Context.
 * @param {string} shaderSource The GLSL source code for the shader.
 * @param {number} shaderType The type of shader, VERTEX_SHADER or
 *     FRAGMENT_SHADER.
 * @return {!WebGLShader} The shader.
 */
export function createShader(gl: WebGL2RenderingContext, source: string, type: number): WebGLShader {
  const shader = gl.createShader(type) as WebGLShader;
  gl.shaderSource(shader, source);
  gl.compileShader(shader);

  const success = gl.getShaderParameter(shader, gl.COMPILE_STATUS);
  if(!success){
    console.error(gl.getShaderInfoLog(shader));
  }

  return shader;
}

/**
 * Creates a program from 2 shaders.
 *
 * @param {!WebGLRenderingContext) gl The WebGL context.
 * @param {!WebGLShader} vertexShader A vertex shader.
 * @param {!WebGLShader} fragmentShader A fragment shader.
 * @return {!WebGLProgram} A program.
 */
export function createProgram(gl: WebGL2RenderingContext, vertexShader: WebGLShader, fragmentShader: WebGLShader): WebGLProgram{
  const program = gl.createProgram() as WebGLProgram;

  gl.attachShader(program, vertexShader);
  gl.attachShader(program, fragmentShader);
  gl.linkProgram(program);

  const success = gl.getProgramParameter(program, gl.LINK_STATUS);
  if(!success){
    console.error(gl.getProgramInfoLog(program));
  }

  return program;
}
