

















import EventBus from '@/EventBus';
import { Component, Vue, Watch } from 'vue-property-decorator';
import FSPoint from '@/intern/_old/FSPoint';

@Component
export default class FSCursor extends Vue{
  $refs!: {
    cur: HTMLCanvasElement[];
    disp: HTMLElement[];
  }

  private pos = new FSPoint();
  private type!: string;
  private tempType!: string;

  @Watch('pos', {deep: true})
  onPosChange(val: FSPoint){
    this.$refs.disp[0].style.left = val.x + "px";
    this.$refs.disp[0].style.top = val.y + "px";
  }

  mounted(){

    // For each canvas set the width and height
    for(const cur of this.$refs.cur){
      cur.width = 21;
      cur.height = 21;
    }

    for(const disp of this.$refs.disp){
      disp.style.height = 21 + "px";
      disp.style.width = 21 + "px";
    }

    // TODO: Draw all cursors.
    this.draw(0);
    // this.draw(1);


    /*
    EventBus.$on('pDn', () =>{
      // TODO: Replace this with property change based on cursor object.
      this.$refs.disp[1].style.left = e.offsetX-10 + "px";
      this.$refs.disp[1].style.top = e.offsetY -10 + "px";
      this.draw(1, e.pressure);
    });
    */
    EventBus.$on('pMv', this.moveCursor)
    EventBus.$on('pOut', (e: PointerEvent) => {
      this.$refs.disp[0].style.left = e.offsetX-10 + "px";
      this.$refs.disp[0].style.top = e.offsetY -10 + "px";
    });
    EventBus.$on('pUp', (e: PointerEvent) => {
      this.type = e.pointerType;
    });
  }

  destroyed(){
    EventBus.$off('pDn');
    EventBus.$off('pUp');
    EventBus.$off('pMv');
  }

  private draw(idx: number, pr = 0){
    // TODO: draw pressure independently on seperate canvas rather than crosshair AND canvas
    // TODO: customize shape
    const can = this.$refs.cur[idx];
    const ctx = can.getContext('2d') as CanvasRenderingContext2D;

    const p = Math.round(pr * 4);
    ctx.clearRect(0, 0, 20, 20);

    ctx.fillRect(0, 10, 21, 1);
    ctx.fillRect(10, 0, 1, 21);

    ctx.clearRect(8 - p, 8 - p, 5 + p * 2, 5 + p * 2);
    ctx.fillRect(10 - p, 10 - p, 1 + p * 2, 1 + p * 2);
    // ctx.fillRect(10 - p, 10 - p, 1 + p * 2, 1 + p * 2);
  }


  private moveCursor(e: PointerEvent){

    // Hack to prevent 'sticky' pointer ===
    if(!this.type){
      this.type = e.pointerType;
    }

    if(this.tempType != e.pointerType){
      this.tempType = e.pointerType;
    }else{
      this.type = this.tempType;
    }

    if(e.pointerType === this.type){
      this.pos = new FSPoint(e.offsetX - 10, e.offsetY - 10);
    }
    // =============================

    // TODO: move this on pen up event and store pen properties on pen object.
    if(e.pressure){
      this.draw(0, e.pressure);
    }else{
      // this.$refs.disp[1].style.left = e.offsetX - 10 + "px";
      // this.$refs.disp[1].style.top = e.offsetY - 10 + "px";
    }
  }

}
